*{
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
.App {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  margin: 5px 15px;
  background-color: rgb(63, 62, 61);
  background-image: url("appMap.jpg");
  min-width: 550px;
  padding: 2px;
}



